import { useCallback } from 'react';

interface ArtifactData {
  namespace: string;
  key: string;
  created_at: string;
  updated_at: string;
  updated_by_userid: string;
}

interface GCFTriggerOptions {
  timeout?: number;
  url?: string;
  artifactsData?: ArtifactData[];
  targetDashboardId?: string;
  uuid?: string;
}

interface GCFTriggerResult {
  success: boolean;
  status?: number;
  statusText?: string;
  error?: string;
}

/**
 * Custom hook to trigger Google Cloud Function
 * Handles CORS, timeouts, and network errors gracefully
 * Sends artifact data as request body
 */
export const useGCFTrigger = () => {
  const triggerGCF = useCallback(async (options: GCFTriggerOptions = {}): Promise<GCFTriggerResult> => {
    // Determine environment based on target dashboard ID
    const { targetDashboardId, uuid } = options;
    const isProduction = targetDashboardId === '3814'; // Production target dashboard ID
    const isUAT = targetDashboardId === '4364'; // UAT target dashboard ID

    const defaultUrl = isProduction
      ? 'https://twbi-dashboard-546150061126.asia-southeast1.run.app'
      : 'https://twbi-dashboard-dev-546150061126.asia-southeast1.run.app';

    const { url = defaultUrl, artifactsData = [] } = options;

    console.log('GCF Trigger Environment:', {
      targetDashboardId,
      isProduction,
      isUAT,
      url,
    });

    try {
      const controller = new AbortController();

      // Prepare request body with artifact data
      const requestBody = {
        artifacts: artifactsData,
        uuid: uuid,
        timestamp: new Date().toISOString(),
      };

      console.log('Sending artifact data to GCF:', requestBody);

      const response = await fetch(url, {
        method: 'POST',
        mode: 'cors',
        credentials: 'omit',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestBody),
        signal: controller.signal,
      });

      console.log('GCF trigger response:', response.status, response.statusText);

      return {
        success: response.ok,
        status: response.status,
        statusText: response.statusText,
      };
    } catch (error) {
      if ((error as Error).name === 'AbortError') {
        console.warn('GCF trigger timed out');
        return {
          success: false,
          error: 'Request timed out',
        };
      } else if ((error as Error).message?.includes('CORS') || (error as Error).message?.includes('network')) {
        console.warn('GCF trigger blocked by CORS/network:', (error as Error).message);
        return {
          success: false,
          error: 'CORS or network error',
        };
      } else {
        console.error('GCF trigger failed:', error);
        return {
          success: false,
          error: (error as Error).message || 'Unknown error',
        };
      }
    }
  }, []);

  return { triggerGCF };
};

export type { ArtifactData, GCFTriggerOptions, GCFTriggerResult };
