import { IUpdateArtifact } from '@looker/sdk';

const ARTIFACT_VALUE_GROUP_KEY = 'Rebate to SKU';

// Environment detection based on actual data field names
function detectEnvironmentFromData(data: any[]): 'testing' | '' {
  if (!data || data.length === 0) {
    console.log('Environment detected: production (no data)');
    return '';
  }

  // Check the first data item for field name patterns
  const firstItem = data[0];
  const fieldNames = Object.keys(firstItem);

  // Look for testing pattern in field names
  const hasTestingFields = fieldNames.some((fieldName) =>
    fieldName.includes('zanalyticstw_bi_rebate_consolidate_v3_testing'),
  );

  if (hasTestingFields) {
    console.log('Environment detected: testing');
    return 'testing';
  }
  console.log('Environment detected: production');
  return '';
}

// Update FieldName enum values based on environment
function getFieldNamesForEnvironment(environment: 'testing' | '') {
  const basePrefix = 'zanalyticstw_bi_rebate_consolidate_v3';
  const prefix = environment === 'testing' ? `${basePrefix}_testing` : basePrefix;

  return {
    ContractGroup: `${prefix}.contract_group`,
    RebateToSKU: `${prefix}.rebate_to_sku`,
    RebateToCustomer: `${prefix}.rebate_to_customer`,
    WoRebate: `${prefix}.weighted_outstanding_rebate`,
    RebateToCategory: `${prefix}.rebate_to_category`,
    RecRebateAmt: `${prefix}.recommended_rebate_amt`,
    CdPercentTotal: `${prefix}.cd_percent_total`,
    SumDimOutstandingRebate: `${prefix}.sum_outstanding_rebate_2`,
    SumOutstandingRebateDmNonFinal: `${prefix}.sum_outstanding_rebate_dm_non_final`,
  };
}

// Get REQUIRED_SAVABLE_FIELD_NAMES based on environment
function getRequiredSavableFieldNames(environment: 'testing' | ''): string[] {
  const fieldNames = getFieldNamesForEnvironment(environment);
  return [
    fieldNames.ContractGroup,
    fieldNames.RebateToCustomer,
    fieldNames.RebateToCategory,
    fieldNames.RebateToSKU,
    fieldNames.CdPercentTotal,
  ];
}

// Get HIDDEN_FIELDS based on environment
export function getHiddenFields(environment: 'testing' | ''): string[] {
  const fieldNames = getFieldNamesForEnvironment(environment);
  return [fieldNames.ContractGroup, fieldNames.SumDimOutstandingRebate, fieldNames.SumOutstandingRebateDmNonFinal];
}

