/* eslint-disable @typescript-eslint/no-explicit-any */

import React, { useEffect, useContext, useState, memo, useRef, useCallback } from 'react';
import {
  Box,
  Table,
  TableHead,
  TableHeaderCell,
  TableBody,
  TableRow,
  TableDataCell,
  Select,
  InputText,
  Space,
  Span,
  Button,
  MultiFunctionButton,
  ButtonOutline,
  Divider,
  Tooltip,
} from '@looker/components';
import {
  Save as IconSave,
  Check as IconCheck,
  FileDownload as IconExport,
  Info as IconInfo,
} from '@styled-icons/material';
import { ExtensionContext40 } from '@looker/extension-sdk-react';
import { IUser } from '@looker/sdk';
import { useGCFTrigger } from './useGCFTrigger';
import type { ArtifactData } from './useGCFTrigger';
import {
  CUSTOM_FIELDS,
  HIDDEN_FIELDS,
  Field,
  CustomerInfo,
  CheckBalanceAll,
  CheckBalanceEach,
  NamespaceArtifactValues,
  InputType,
  calculateSavedArtifactValues,
  sortAndGroupQueryData,
  getSavableArtifacts,
  decodeArtifactValue,
  getFilteredObject,
  encodeFilteredObject,
  chunk,
  updateCheckBalanceAll,
  debounce,
  sha256,
} from './HandleData';

const enum SaveState {
  Unsaved = 'unsaved',
  Saving = 'saving',
  Saved = 'saved',
}

const enum SyncState {
  NotSynced = 'not_synced',
  Syncing = 'syncing',
  Synced = 'synced',
}

function formatBalance(value: number) {
  return Math.round(value).toLocaleString();
}

/**
 * Generate a UUID v4
 */
function generateUUID(): string {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    const r = (Math.random() * 16) | 0;
    const v = c === 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}

/**
 * Get UUID from dashboardFilters or URL parameters
 */
function getUUIDFromFilters(dashboardFilters: Record<string, any>): string | null {
  // Check if UUID exists in dashboardFilters and has a valid value (check both cases)
  if (
    dashboardFilters &&
    ((dashboardFilters.uuid &&
      dashboardFilters.uuid !== '' &&
      dashboardFilters.uuid !== null &&
      dashboardFilters.uuid !== undefined) ||
      (dashboardFilters.UUID &&
        dashboardFilters.UUID !== '' &&
        dashboardFilters.UUID !== null &&
        dashboardFilters.UUID !== undefined))
  ) {
    return dashboardFilters.uuid || dashboardFilters.UUID;
  }

  // Check if UUID exists in URL parameters and has a valid value (check both cases)
  const urlParams = new URLSearchParams(window.location.search);
  const urlUuid = urlParams.get('UUID') || urlParams.get('uuid');
  if (urlUuid && urlUuid !== '' && urlUuid !== 'null' && urlUuid !== 'undefined') {
    return urlUuid;
  }

  return null;
}

/**
 * Generate dashboard URL with current filters and UUID parameter
 */
function generateDashboardURLWithFiltersAndUUID(
  dashboardId: string,
  dashboardFilters: Record<string, any>,
  uuid: string,
): string {
  const baseUrl = 'https://lookerdev.zuelligpharma.com';
  const dashboardUrl = `${baseUrl}/dashboards/${dashboardId}`;
  const url = new URL(dashboardUrl);

  // Add all current dashboard filters
  Object.entries(dashboardFilters).forEach(([key, value]) => {
    if (key && value && value !== '' && value !== '-NULL') {
      url.searchParams.set(key, String(value));
    }
  });

  // Add UUID parameter
  url.searchParams.set('UUID', uuid);

  return url.toString();
}

const RebateTable = () => {
  const { extensionSDK, visualizationData, coreSDK, tileHostData } = useContext(ExtensionContext40);
  const { triggerGCF } = useGCFTrigger();
  const [fields, setFields] = useState<Field[]>([]);
  const [userInfo, setUserInfo] = useState<IUser>();
  const [customerInfos, setCustomerInfos] = useState<CustomerInfo[]>([]);
  const [artifactKeys, setArtifactKeys] = useState<string>();
  const [savedArtifacts, setSavedArtifacts] = useState<NamespaceArtifactValues>({});
  const [artifactNS, setArtifactNS] = useState<string>('');
  const [errMsg, setErrMsg] = useState('');
  const [saveState, setSaveState] = useState<SaveState>(SaveState.Unsaved);
  const [syncState, setSyncState] = useState<SyncState>(SyncState.NotSynced);
  const [isSyncing, setIsSyncing] = useState(false);
  const [exportLoading, setExportLoading] = useState(false);
  const [hasValidUUIDState, setHasValidUUIDState] = useState(false);
  const artifactsRef = useRef<NamespaceArtifactValues>({});
  const [checkBalance, setCheckBalance] = useState<CheckBalanceAll>({
    _all: {
      total: { total: 0, used: 0, remaining: 0 },
      dm: { total: 0, used: 0, remaining: 0 },
      nonDm: { total: 0, used: 0, remaining: 0 },
      contractGroup: '',
    },
  });

  const saveRefArtifacts = useCallback(
    (customer: string, uid: string, data: Record<string, any>) => {
      artifactsRef.current = {
        ...artifactsRef.current,
        [customer]: {
          value: {
            ...artifactsRef.current[customer]?.value,
            [uid]: {
              ...artifactsRef.current[customer]?.value?.[uid],
              ...data,
            },
          },
          version: -1,
        },
      };
      // Only reset save state and sync state when there are pending changes and we're not currently saving/syncing
      if (saveState !== SaveState.Saving && syncState !== SyncState.Syncing) {
        setSaveState(SaveState.Unsaved);
        setSyncState(SyncState.NotSynced);
      }
    },
    [saveState, syncState],
  );

  /**
   * Check if UUID exists and is valid
   */
  const hasValidUUID = useCallback(() => {
    return hasValidUUIDState;
  }, [hasValidUUIDState]);

  // Update UUID validity when tileHostData changes
  useEffect(() => {
    const dashboardFilters = (tileHostData as any)?.dashboardFilters || {};
    const uuid = getUUIDFromFilters(dashboardFilters);
    const isValid = uuid !== null;
    console.log('UUID check:', { dashboardFilters, uuid, hasValid: isValid });
    setHasValidUUIDState(isValid);
  }, [tileHostData]);

  /**
   * Generate new section with UUID
   */
  const generateNewSection = useCallback(async () => {
    const uuid = generateUUID();
    const currentDashboardId = tileHostData.dashboardId?.toString();

    if (!currentDashboardId) {
      alert('Unable to determine current dashboard ID. Please try again.');
      return;
    }

    // Get current dashboard filters
    const dashboardFilters: Record<string, any> = {};
    if (tileHostData && (tileHostData as any).dashboardFilters) {
      Object.entries((tileHostData as any).dashboardFilters).forEach(([key, value]) => {
        if (key && value && value !== '' && value !== '-NULL') {
          dashboardFilters[key] = value;
        }
      });
    }

    const newUrl = generateDashboardURLWithFiltersAndUUID(currentDashboardId, dashboardFilters, uuid);
    console.log('Generated new section URL:', newUrl);

    // Try to open URL using extensionSDK.openBrowserWindow
    try {
      if (extensionSDK && typeof extensionSDK.openBrowserWindow === 'function') {
        await extensionSDK.openBrowserWindow(newUrl, '_blank');
      } else {
        throw new Error('openBrowserWindow not available');
      }
    } catch (error) {
      console.error('Failed to open browser window:', error);
      // Fallback: copy URL to clipboard
      try {
        if (navigator.clipboard) {
          await navigator.clipboard.writeText(newUrl);
        } else {
          alert(`Please navigate to:\n\n${newUrl}\n\nCopy this URL and open it in a new tab.`);
        }
      } catch {
        alert(`Please navigate to:\n\n${newUrl}\n\nCopy this URL and open it in a new tab.`);
      }
    }
  }, [extensionSDK, tileHostData.dashboardId]);

  /**
   * Sync function: Save data and trigger GCF
   */
  const syncData = async () => {
    if (isSyncing || !visualizationData) return;
    console.log('tileHostData', tileHostData, extensionSDK.getContextData());

    // Set syncing state immediately
    setIsSyncing(true);
    setSyncState(SyncState.Syncing);

    try {
      // Get UUID for artifacts
      const dashboardFilters = (tileHostData as any)?.dashboardFilters || {};
      const uuid = getUUIDFromFilters(dashboardFilters);

      const data = getSavableArtifacts(
        artifactsRef.current,
        savedArtifacts,
        customerInfos,
        getFilteredObject(visualizationData.queryResponse.applied_filters),
        visualizationData.queryResponse.data,
        uuid || undefined,
      );

      if (data.length > 0) {
        // Save data first
        setSaveState(SaveState.Saving);
        const res = await coreSDK.ok(coreSDK.update_artifacts(artifactNS, data));
        const newArtifacts = { ...savedArtifacts };
        res.forEach((item) => {
          newArtifacts[item.key] = {
            value: decodeArtifactValue(item.value),
            version: item.version as number,
          };
        });
        const { artifactValues, checkBalanceValues } = calculateSavedArtifactValues(
          customerInfos,
          newArtifacts,
          visualizationData.queryResponse.data,
        );
        setSavedArtifacts(artifactValues);
        setCheckBalance(checkBalanceValues);
        artifactsRef.current = {};
        setSaveState(SaveState.Saved);

        const artifactsData: ArtifactData[] = res.map((item) => ({
          namespace: String(item.namespace || artifactNS),
          key: String(item.key),
          created_at:
            item.created_at instanceof Date
              ? item.created_at.toISOString()
              : String(item.created_at || new Date().toISOString()),
          updated_at:
            item.updated_at instanceof Date
              ? item.updated_at.toISOString()
              : String(item.updated_at || new Date().toISOString()),
          updated_by_userid: String(item.updated_by_userid || userInfo?.id || 'unknown'),
        }));

        // Determine target dashboard ID for GCF trigger
        const currentDashboardId = tileHostData.dashboardId?.toString();
        let targetDashboardId = '3814'; // Default to production
        if (currentDashboardId === '4362') {
          targetDashboardId = '4364'; // UAT
        } else if (currentDashboardId === '2232') {
          targetDashboardId = '3814'; // Production
        }

        const gcfResult = await triggerGCF({ artifactsData, targetDashboardId, uuid: uuid || undefined });
        if (gcfResult.success) {
          setSyncState(SyncState.Synced);
        } else {
          console.warn('GCF trigger failed:', gcfResult.error);
          setSyncState(SyncState.NotSynced);
        }
        setIsSyncing(false);
      } else {
        // No data to save, just trigger GCF without artifact data
        // Determine target dashboard ID for GCF trigger
        const currentDashboardId = tileHostData.dashboardId?.toString();
        let targetDashboardId = '3814'; // Default to production
        if (currentDashboardId === '4362') {
          targetDashboardId = '4364'; // UAT
        } else if (currentDashboardId === '2232') {
          targetDashboardId = '3814'; // Production
        }

        // Get UUID for GCF trigger
        const dashboardFilters = (tileHostData as any)?.dashboardFilters || {};
        const uuid = getUUIDFromFilters(dashboardFilters);

        const gcfResult = await triggerGCF({ targetDashboardId, uuid: uuid || undefined });
        if (gcfResult.success) {
          setSyncState(SyncState.Synced);
          setSaveState(SaveState.Saved);
        } else {
          console.warn('GCF trigger failed:', gcfResult.error);
          setSyncState(SyncState.NotSynced);
          setSaveState(SaveState.Saved);
        }
        setIsSyncing(false);
      }
    } catch (error) {
      setSaveState(SaveState.Unsaved);
      setSyncState(SyncState.NotSynced);
      setIsSyncing(false);
      alert('An error occurred while syncing data. Please try again.');
      console.error('syncData', error);
    }
  };

  /**
   * Export function: Navigate to target dashboard (only works after sync)
   */
  const exportData = async () => {
    if (exportLoading) return;

    try {
      setExportLoading(true);
      const currentDashboardId = tileHostData.dashboardId?.toString();
      let targetDashboardId = '3814';

      // Uat env
      if (currentDashboardId === '4362') {
        targetDashboardId = '4364';
        // Product env
      } else if (currentDashboardId === '2232') {
        targetDashboardId = '3814';
      }

      const dashboardUrl = `https://lookerdev.zuelligpharma.com/dashboards/${targetDashboardId}`;

      const dashboardFilters: Record<string, any> = {};

      // Extract filters from tileHostData - this contains all the filters we need
      if (tileHostData && (tileHostData as any).dashboardFilters) {
        Object.entries((tileHostData as any).dashboardFilters).forEach(([key, value]) => {
          if (key && value && value !== '' && value !== '-NULL') {
            dashboardFilters[key] = value;
          }
        });
      }

      const filterNameMapping: Record<string, string> = {
        // Date filters - exact matches from your dashboardFilter
        'Rebate Tracking Start': 'Rebate Tracking Start',
        'Rebate Tracking End': 'Rebate Tracking End',
        'CD Period End (Rebate To)': 'CD Period End',
        'CD Period Start (Rebate To)': 'CD Period Start',
        'Rebate Planning Start': 'Rebate Planning Start',
        'Rebate Planning End': 'Rebate Planning End',
        'Select Tracking End Date Default': 'Select Tracking End Date Default',

        // Customer and Contract filters
        'For Contract': 'Contract Group',
        'Contract Group ': 'Contract Group',
        'For Customers': 'Customer',
        'Rebate to: Customer': 'Rebate to: Customer',

        // SKU filters
        'For SKU': 'For SKU',
        'SKU ': 'SKU',
        'Rebate to: Cust SKU ': 'Rebate to: Cust SKU',
        'Rebate to Cust SKU': 'Rebate to: Cust SKU',

        // Other filters
        'Actual Rebate Period': 'Actual Rebate Period',
        'Alert - Actual Rebate Until': 'Alert - Actual Rebate Until',
        'Date Selection': 'Date Selection',
        'Completed Contracts': 'Completed Contracts',
        'Non-Rebate Products': 'Non-Rebate Products',
        'IRIS User Filter': 'IRIS User Filter',
        'PG ': 'PG',
        'KAM ': 'KAM',
      };

      const filterParams = Object.entries(dashboardFilters)
        .filter(([, value]) => {
          if (!value || value === '' || value === '-NULL') return false;
          return true;
        })
        .map(([filterKey, value]) => {
          const mappedKey = filterNameMapping[filterKey] || filterKey;

          const encodedKey = encodeURIComponent(mappedKey).replace(/%20/g, '+');
          const encodedValue = encodeURIComponent(String(value)).replace(/%20/g, '+');
          return `${encodedKey}=${encodedValue}`;
        })
        .join('&');

      const finalUrl = filterParams ? `${dashboardUrl}?${filterParams}` : dashboardUrl;

      try {
        if (extensionSDK && typeof extensionSDK.openBrowserWindow === 'function') {
          await extensionSDK.openBrowserWindow(finalUrl, '_blank');
        } else {
          // Fallback: copy URL to clipboard
          if (navigator.clipboard) {
            await navigator.clipboard.writeText(finalUrl);
            alert(`Dashboard URL has been copied to your clipboard:\n\n${finalUrl}\n\nPlease paste it in a new tab.`);
          } else {
            alert(`Please navigate to:\n\n${finalUrl}`);
          }
        }
      } catch (error) {
        console.log('Navigation failed:', error);
        // Fallback: copy URL to clipboard
        try {
          if (navigator.clipboard) {
            await navigator.clipboard.writeText(finalUrl);
            alert(
              `Navigation failed. Dashboard URL has been copied to your clipboard:\n\n${finalUrl}\n\nPlease paste it in a new tab.`,
            );
          } else {
            alert(`Navigation failed. Please navigate to:\n\n${finalUrl}`);
          }
        } catch {
          alert(`Navigation failed. Please navigate to:\n\n${finalUrl}`);
        }
      }
    } catch (error) {
      console.error('Export failed:', error);
      alert('An error occurred during export. Please try again.');
    } finally {
      setExportLoading(false);
    }
  };

  const fetchSavedArtifacts = async (ns: string, keys: string) => {
    try {
      const artifacts = await (async () => {
        const keyBatches = chunk(keys.split(','), 10);
        const result = await Promise.all(
          keyBatches.map(async (ks) => {
            return coreSDK.ok(coreSDK.artifact({ namespace: ns, key: ks.join(','), fields: 'key,value,version' }));
          }),
        );
        return result.flat();
      })();
      const reduced = artifacts.reduce(
        (acc, cur) => ({
          ...acc,
          [cur.key]: { value: decodeArtifactValue(cur.value), version: cur.version },
        }),
        {},
      );
      const { artifactValues, checkBalanceValues } = calculateSavedArtifactValues(
        customerInfos,
        reduced,
        visualizationData?.queryResponse.data,
      );
      setSavedArtifacts(artifactValues);
      setCheckBalance(checkBalanceValues);
    } catch (error) {
      setErrMsg('An error occurred while getting artifacts. Please try again.');
      console.error('artifacts', error);
    }
  };

  const recalculateCheckBalance = (customerName: string, changed: CheckBalanceEach) => {
    const newCheckBalance = updateCheckBalanceAll(customerName, checkBalance, changed);
    setCheckBalance(newCheckBalance);
  };

  const calculateBalances = (
    customerInfo: CustomerInfo,
    uid: string,
    data: Record<string, any>,
    cb: (newValue: Record<string, any>) => void,
  ) => {
    const { artifactValues, checkBalanceValues } = calculateSavedArtifactValues(
      [customerInfo],
      {
        [customerInfo.customer]: { value: data, version: -1 },
      },
      visualizationData?.queryResponse.data,
    );
    recalculateCheckBalance(customerInfo.customer, checkBalanceValues[customerInfo.customer]);
    saveRefArtifacts(customerInfo.customer, uid, data[uid]);
    cb(artifactValues[customerInfo.customer].value);
  };

  const calculateBalancesDebounced = debounce(calculateBalances, 500);

  useEffect(() => {
    const getMe = async () => {
      try {
        const me = await coreSDK.ok(coreSDK.me());
        setUserInfo(me);
      } catch (error) {
        setErrMsg('An error occurred while getting user information. Please try again.');
        console.error('me', error);
      }
    };
    getMe().then(() => {
      extensionSDK.rendered();
    });
  }, []);

  // sort & group query response data
  useEffect(() => {
    if (!visualizationData?.queryResponse || !userInfo) return;
    const displayedFields: Field[] = [
      ...visualizationData.queryResponse.fields['dimensions'],
      ...visualizationData.queryResponse.fields['measures'],
    ].map<Field>((item) => ({
      label: item['label_short'],
      name: item['name'],
      align: item['align'],
      hidden: HIDDEN_FIELDS.includes(item['name']),
    }));
    const custInfo = sortAndGroupQueryData(visualizationData.queryResponse.data, displayedFields);
    setFields(displayedFields);
    setCustomerInfos(custInfo);
    setArtifactKeys(custInfo.map((item) => item.customer).join(','));
    const encodedFilter = encodeFilteredObject(visualizationData.queryResponse.applied_filters);
    sha256(encodedFilter).then(async (hashedFilter) => {
      const dashboardFilters = (tileHostData as any)?.dashboardFilters || {};
      const uuid = getUUIDFromFilters(dashboardFilters);
      if (uuid) {
        const namespace = `tw_bi_rebate_ext_${tileHostData.dashboardId}_${tileHostData.elementId}_${uuid}_${hashedFilter}`;
        setArtifactNS(namespace);
      }
    });
  }, [visualizationData, userInfo]);

  // load savedArtifacts
  useEffect(() => {
    if (!artifactKeys || !artifactNS) return;
    fetchSavedArtifacts(artifactNS, artifactKeys);
  }, [artifactKeys, artifactNS]);

  return (
    <Box p="u4" height="100%">
      {errMsg ? (
        <Space align="center" justify="center">
          <Span fontSize="xxlarge" color="critical">
            {errMsg}
          </Span>
        </Space>
      ) : (
        <Box height="100%">
          <Space justify="end" between={true}>
            <Box width="100%" display="flex" justifyContent="end" alignContent="end" alignItems="center">
              <Tooltip
                content="To view the result in dashboard 2, click the 'Save' button and wait for the data to be synced. After the syncing, click to Export to be redirected and view result in dashboard 2"
                placement="bottom-end"
                maxWidth="400px"
              >
                <IconInfo
                  size="20px"
                  style={{ cursor: 'pointer', color: '#d3d3d3', position: 'absolute', top: '35px', right: '20px' }}
                />
              </Tooltip>
            </Box>
          </Space>
          <Space between={true} py="u2">
            <Box border="ui3">
              <Box p="u1" bg="ui1">
                <Span>Check Balance</Span>
              </Box>
              <Divider />
              <Box p="u1">
                <Span>
                  Total Balance = {formatBalance(checkBalance._all.total.total)} -{' '}
                  {formatBalance(checkBalance._all.total.used)} = {formatBalance(checkBalance._all.total.remaining)}
                </Span>
              </Box>
              <Divider />
              <Box p="u1">
                <Span>
                  DM Balance = {formatBalance(checkBalance._all.dm.total)} - {formatBalance(checkBalance._all.dm.used)}{' '}
                  = {formatBalance(checkBalance._all.dm.remaining)}
                </Span>
              </Box>
              <Divider />
              <Box p="u1">
                <Span>
                  Non-DM Balance = {formatBalance(checkBalance._all.nonDm.total)} -{' '}
                  {formatBalance(checkBalance._all.nonDm.used)} = {formatBalance(checkBalance._all.nonDm.remaining)}
                </Span>
              </Box>
            </Box>
            <Space justify="end" align="start">
              {!hasValidUUID() && (
                <Button onClick={generateNewSection} width={150} iconBefore={<IconInfo />}>
                  Generate Section
                </Button>
              )}

              <MultiFunctionButton swap={syncState === SyncState.Synced} alternate={SyncedButton}>
                <Button
                  onClick={syncData}
                  width={130}
                  disabled={isSyncing || !hasValidUUID()}
                  iconBefore={<IconSave />}
                >
                  {isSyncing ? 'Saving...' : 'Save'}
                </Button>
              </MultiFunctionButton>

              <Button
                onClick={exportData}
                width={130}
                iconBefore={<IconExport />}
                disabled={exportLoading || !hasValidUUID()}
              >
                <Tooltip
                  content={
                    hasValidUUID()
                      ? "To view the result in dashboard 2, click the 'Save' button and wait for the data to be saved. After the saving, click to Export to be redirected and view result in dashboard 2"
                      : "Please click 'Generate Section' button first to create a unique session ID before saving or exporting data."
                  }
                  placement="bottom-end"
                  maxWidth="400px"
                >
                  <span>{exportLoading ? 'Exporting...' : 'Export'}</span>
                </Tooltip>
              </Button>
            </Space>
          </Space>
          <Box height="calc(100% - 120px)" overflow="auto">
            <Table className="rebate-table" mt="u2">
              <TableHead>
                <TableRow>
                  {[...fields, ...CUSTOM_FIELDS]
                    .filter((f) => !f.hidden)
                    .map((f) => (
                      <TableHeaderCell p="u1" textAlign={f.align} key={f.name} border bg="ui1">
                        {f.label}
                      </TableHeaderCell>
                    ))}
                </TableRow>
              </TableHead>
              <TableBody fontSize={'xsmall'}>
                {customerInfos.map((customerInfo) => (
                  <RebateToCustomer
                    fields={fields}
                    customerInfo={customerInfo}
                    savedData={savedArtifacts[customerInfo.customer]?.value}
                    key={customerInfo.customer}
                    calculateBalances={calculateBalancesDebounced}
                  />
                ))}
              </TableBody>
            </Table>
          </Box>
        </Box>
      )}
    </Box>
  );
};

function formatAmount(input: string | number): string {
  input = String(input);
  if (!input) return '';
  const negativePrefix = input.startsWith('-') ? '-' : '';
  const point = input.indexOf('.') > -1 ? '.' : '';
  const [int, frac] = input.replace(/[^\d.]/g, '').split('.');
  return `${negativePrefix}${int ? Number(int).toLocaleString() : ''}${point}${frac || ''}`;
}

function amountNumber(input: string | number): number {
  if ('number' === typeof input) {
    return input;
  }
  if (input?.endsWith('.')) {
    input = input?.substring(0, input?.length - 1);
  }
  input = input?.replace(/,/g, '');
  return Number(input) || 0;
}

function normalizeRebateData(data: Record<string, any>): Record<string, any> {
  const output: Record<string, any> = {};
  Object.keys(data).forEach((uid: string) => {
    const values = data[uid];
    output[uid] = { ...values };
    Object.keys(values).forEach((fieldName: string) => {
      const field = CUSTOM_FIELDS.find((item) => item.name === fieldName);
      if (field?.type === InputType.Number) {
        output[uid][fieldName] = amountNumber(values[fieldName]);
      }
    });
  });

  return output;
}

const RebateToCustomer = memo(function RebateToCustomer({
  fields,
  customerInfo,
  savedData,
  calculateBalances,
}: {
  fields: Field[];
  customerInfo: CustomerInfo;
  savedData: Record<string, any>;
  calculateBalances: (
    customer: CustomerInfo,
    uid: string,
    data: Record<string, any>,
    cb: (newValue: Record<string, any>) => void,
  ) => void;
}) {
  const [localValues, setLocalValues] = useState(savedData);

  useEffect(() => {
    setLocalValues(savedData);
  }, [savedData]);

  const saveDataLocal = (uid: string, field: Field, data: Record<string, any>) => {
    const oldValue = localValues?.[uid]?.[field?.name];
    const newData = { ...localValues };
    newData[uid] = {
      ...newData[uid],
      ...data,
    };
    setLocalValues(newData);
    if (field.type === InputType.Number) {
      if (amountNumber(oldValue) === amountNumber(data?.[field.name])) {
        return;
      }
    }
    calculateBalances(customerInfo, uid, normalizeRebateData(newData), function (newValue: Record<string, any>) {
      if (newValue) {
        setLocalValues(newValue);
      }
    });
  };

  return (
    <>
      {customerInfo.skuInfos.map((skuInfo) => (
        <TableRow key={`${customerInfo.customer}_${skuInfo.uidKey}`}>
          {fields.map((fItem) =>
            skuInfo.fieldsData[fItem.name].rowSpan ? (
              <TableDataCell
                border
                p="u1"
                textAlign={skuInfo.fieldsData[fItem.name].align}
                verticalAlign={skuInfo.fieldsData[fItem.name].verticalAlign || 'middle'}
                rowSpan={skuInfo.fieldsData[fItem.name].rowSpan}
                key={fItem.name}
              >
                {skuInfo.fieldsData[fItem.name].rendered}
              </TableDataCell>
            ) : (
              <></>
            ),
          )}
          {CUSTOM_FIELDS.map((f) => (
            <TableDataCell
              border
              p="u1"
              textAlign={f.align}
              verticalAlign="middle"
              minWidth={f.minWidth}
              maxWidth={f.maxWidth}
              key={`${customerInfo.customer}_${skuInfo.uidKey}_${f.name}`}
            >
              <CustomField
                field={f}
                uid={skuInfo.uidKey}
                data={localValues?.[skuInfo.uidKey]}
                saveDataLocal={saveDataLocal}
              />
            </TableDataCell>
          ))}
        </TableRow>
      ))}
    </>
  );
});

const CustomField = ({
  field,
  uid,
  data,
  saveDataLocal,
}: {
  field: Field;
  uid: string;
  data: any;
  saveDataLocal: (uid: string, field: Field, data: Record<string, any>) => void;
}) => {
  let localValue = data?.[field.name] ?? field.defaultValue;
  if (field.type === InputType.Number) {
    localValue = formatAmount(localValue);
  }
  const rendered = field.render ? field.render(localValue) : localValue;

  return (
    <>
      {field.type === InputType.Text && <Span>{rendered}</Span>}
      {field.type === InputType.Select && (
        <Select
          minWidth={100}
          value={localValue}
          options={field.options}
          onChange={(value) => saveDataLocal(uid, field, { [field.name]: value })}
        />
      )}
      {field.type === InputType.Number && (
        <InputText
          type="text"
          minWidth={100}
          value={localValue}
          onChange={(e) => saveDataLocal(uid, field, { [field.name]: e.target.value })}
        />
      )}
    </>
  );
};

const SyncedButton = (
  <ButtonOutline iconBefore={<IconCheck />} width={130}>
    Data Saved
  </ButtonOutline>
);

export default RebateTable;
